<template>
  <div ref="wrapper" class="page_wraper">
    <slot></slot>
  </div>
</template>

<script >
  import BScroll from 'better-scroll'
  export default {
    props: {
      /*1 scroll
       2 scroll
       3 scrollswipescroll     */
      probeType: {
        type: Number,
        default: 3
      },
      //  click
      click: {
        type: Boolean,
        default: true
      },
      //  
      scrollX: {
        type: Boolean,
        default: false
      },
      //  
      listenScroll: {
        type: Boolean,
        default: false
      },
      //  
      data: {
        type: Array,
        default: null
      },
      /** *  */
      pullup: {
        type: Boolean,
        default: true
      },
      /** *  */
      pulldown: {
        type: Boolean,
        default: true
      },
      /** *  */
      beforeScroll: {
        type: Boolean,
        default: true
      },
      /** * scroll */
      refreshDelay: {
        type: Number,
        default: 20
      },
      /** *  */
      pullDownRefresh: {
        type: Boolean,
        default: true
      },
      /** *  */
      pullUpLoad: {
        type: Boolean,
        default: true
      },
    },
    mounted() {
      // DOMbetter-scroll
      setTimeout(() => {
        this._initScroll()
      }, 20)
    },
    methods: {
      _initScroll() {
        if (!this.$refs.wrapper) {
          return
        }
        // better-scroll
        this.scroll = new BScroll(this.$refs.wrapper, {
          probeType: this.probeType,
          click: this.click,
          scrollX: this.scrollX,
          pullDownRefresh:this.pullDownRefresh,
          pullUpLoad:this.pullUpLoad
        })
        // 
        if (this.listenScroll) {
          let me = this
          this.scroll.on('scroll', (pos) => {
            me.$emit('scroll', pos)
          })
        }
        //
        if (this.pullup) {
          this.scroll.on('scrollEnd', () => { // 
            if (this.scroll.y <= (this.scroll.maxScrollY + 50)) {
              this.$emit('scrollToEnd')
            }
          })
        }
        //
        if (this.pulldown) {
          this.scroll.on('touchEnd', (pos) => { // 
            if (pos.y > 50) {
              this.$emit('pulldown',pos)
            }
          })
        }
        //
        if (this.pullup) {
          this.scroll.on('touchEnd', (pos) => { // 
            if (pos.y < -50) {
              this.$emit('pullup',pos)
            }
          })
        }
        // 
        if (this.beforeScroll) {
          this.scroll.on('beforeScrollStart', () => {
            this.$emit('beforeScroll')
          })
        }
        if (this.pullDownRefresh){
          this.scroll.on('pullingDown', () => {
            this.$emit('pullingDown')
          })
        }
        if (this.pullUpLoad){
          this.scroll.on('pullingUp', () => {
            this.$emit('pullingUp')
          })
        }

      },
      disable() {
        // better-scrolldisable
        this.scroll && this.scroll.disable()
      },
      enable() {
        // better-scrollenable
        this.scroll && this.scroll.enable()
      },
      refresh() {
        // better-scrollrefresh
        this.scroll && this.scroll.refresh()
      },
      scrollTo() {
        // better-scrollscrollTo
        this.scroll && this.scroll.scrollTo.apply(this.scroll, arguments)
      },
      scrollToElement() {
        // better-scrollscrollToElement
        this.scroll && this.scroll.scrollToElement.apply(this.scroll, arguments)
      },
      /*  */
      finishPullDown(){
        this.scroll && this.scroll.finishPullDown();
      },
      /*  */
      finishPullUp() {
        this.scroll && this.scroll.finishPullUp();
      }
    },
    watch: {
      // refreshDelayrefresh
      data() {
        setTimeout(() => {
          this.refresh()
        }, this.refreshDelay)
      }
    }
  }
</script>